___CSS_LOADER_EXPORT___.push([module.id, `body {
  font-family: Arial, Helvetica, sans-serif;
  color: rgb(92, 92, 92);
}

.app {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}

.modal_container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  min-width: 300px;
  gap: 20px;
  padding: 20px;
  border: 1px solid gray;
  border-radius: 10px;
}

.modal_title {
  margin: 0 auto;
  font-size: 16px;
}

.modal_content {
  width: 100%;
  height: 25px;
  box-sizing: border-box;
  border: 1px solid gray;
  border-radius: 5px;
  font-family: inherit;
  color: inherit;
}

.modal_btn {
  width: 150px;
  height: 25px;
  margin: 0 auto;
  background-color: white;
  border: 1px solid gray;
  border-radius: 5px;
  font-family: inherit;
  color: inherit;
}

.error {
  color: red;
  margin: 0 auto;
}

.users_container {
  display: flex;
  flex-direction: column;
  gap: 10px;
  min-width: 20%;
  margin-top: 10px;
  padding: 10px;
  border: 1px solid gray;
  border-right: none;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}

.user_item {
  display: flex;
  flex-direction: row;
  gap: 10px;
  align-items: center;
}

.user_icon {
  width: 30px;
  height: 30px;
  border: 1px solid gray;
  border-radius: 50%;
}

.chat_container {
  width: 70%;
  height: 90vh;
  display: flex;
  flex-direction: column;
  gap: 20px;
  justify-content: end;
  padding: 10px;
  border: 1px solid gray;
  border-radius: 10px;
}

.messages {
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.message_info {
  margin-bottom: 5px;
  color: gray;
  font-size: 12px;
}

.message_form {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.message_input {
  width: 78%;
  height: 25px;
  box-sizing: border-box;
  border: 1px solid gray;
  border-radius: 5px;
  font-family: inherit;
  color: inherit;
}

.btn {
  width: 20%;
  height: 25px;
  background-color: white;
  border: 1px solid gray;
  border-radius: 5px;
  font-family: inherit;
  color: inherit;
}`, "",{"version":3,"sources":["webpack://./src/css/style.css"],"names":[],"mappings":"AAAA;EACE,yCAAyC;EACzC,sBAAsB;AACxB;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,uBAAuB;AACzB;;AAEA;EACE,kBAAkB;EAClB,QAAQ;EACR,SAAS;EACT,gCAAgC;EAChC,aAAa;EACb,sBAAsB;EACtB,gBAAgB;EAChB,SAAS;EACT,aAAa;EACb,sBAAsB;EACtB,mBAAmB;AACrB;;AAEA;EACE,cAAc;EACd,eAAe;AACjB;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,sBAAsB;EACtB,sBAAsB;EACtB,kBAAkB;EAClB,oBAAoB;EACpB,cAAc;AAChB;;AAEA;EACE,YAAY;EACZ,YAAY;EACZ,cAAc;EACd,uBAAuB;EACvB,sBAAsB;EACtB,kBAAkB;EAClB,oBAAoB;EACpB,cAAc;AAChB;;AAEA;EACE,UAAU;EACV,cAAc;AAChB;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,SAAS;EACT,cAAc;EACd,gBAAgB;EAChB,aAAa;EACb,sBAAsB;EACtB,kBAAkB;EAClB,4BAA4B;EAC5B,+BAA+B;AACjC;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,SAAS;EACT,mBAAmB;AACrB;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,sBAAsB;EACtB,kBAAkB;AACpB;;AAEA;EACE,UAAU;EACV,YAAY;EACZ,aAAa;EACb,sBAAsB;EACtB,SAAS;EACT,oBAAoB;EACpB,aAAa;EACb,sBAAsB;EACtB,mBAAmB;AACrB;;AAEA;EACE,kBAAkB;EAClB,aAAa;EACb,sBAAsB;EACtB,SAAS;AACX;;AAEA;EACE,kBAAkB;EAClB,WAAW;EACX,eAAe;AACjB;;AAEA;EACE,aAAa;EACb,8BAA8B;EAC9B,mBAAmB;AACrB;;AAEA;EACE,UAAU;EACV,YAAY;EACZ,sBAAsB;EACtB,sBAAsB;EACtB,kBAAkB;EAClB,oBAAoB;EACpB,cAAc;AAChB;;AAEA;EACE,UAAU;EACV,YAAY;EACZ,uBAAuB;EACvB,sBAAsB;EACtB,kBAAkB;EAClB,oBAAoB;EACpB,cAAc;AAChB","sourcesContent":["body {\n  font-family: Arial, Helvetica, sans-serif;\n  color: rgb(92, 92, 92);\n}\n\n.app {\n  display: flex;\n  flex-direction: row;\n  align-items: flex-start;\n}\n\n.modal_container {\n  position: absolute;\n  top: 50%;\n  left: 50%;\n  transform: translate(-50%, -50%);\n  display: flex;\n  flex-direction: column;\n  min-width: 300px;\n  gap: 20px;\n  padding: 20px;\n  border: 1px solid gray;\n  border-radius: 10px;\n}\n\n.modal_title {\n  margin: 0 auto;\n  font-size: 16px;\n}\n\n.modal_content {\n  width: 100%;\n  height: 25px;\n  box-sizing: border-box;\n  border: 1px solid gray;\n  border-radius: 5px;\n  font-family: inherit;\n  color: inherit;\n}\n\n.modal_btn {\n  width: 150px;\n  height: 25px;\n 